import React, { Component } from 'react';
import './App.css';
import './Buttons.css'
import { Button } from 'react-bootstrap';
import autoBind from 'react-autobind';
import 'tui-image-editor/dist/tui-image-editor.css';
import 'tui-color-picker/dist/tui-color-picker.css';
import ImageEditor from '@toast-ui/react-image-editor';



class App extends Component {
	editorRef = React.createRef();
	video = React.createRef();
	active = false;
	state = {
		mode: 'start',
		export: '',
		img: ''
	}

	capture(){
		navigator.mediaDevices.getDisplayMedia({
			video: {
				cursor: 'never'
			},
			audio: false
		}).then(stream =>{
			this.active = true;
			this.video.current.srcObject = stream;
			stream.oninactive = () => {
				this.active = false;
				if(this.state.mode === 'capture')this.setState({mode:'start'})
			};
			this.setState({
				mode: 'capture'
			});
		});
	}

	stop(){
		let tracks = this.video.current.srcObject.getTracks();
		tracks.forEach(track => track.stop());
		this.video.current.srcObject = null;

	}
	take(){
		let canvas = document.createElement('canvas');
		canvas.width = this.video.current.videoWidth;
		canvas.height = this.video.current.videoHeight;
		let ctx = canvas.getContext('2d');
		ctx.drawImage(this.video.current, 0, 0, canvas.width, canvas.height);
		let img = canvas.toDataURL();
		
		this.editor.ui.initializeImgUrl = img;
		this.editor._mainAction().reset()
		setTimeout(() => this.editor.ui._initMenu())
		//this.editor.loadImageFromURL(img, 'Screenshot');
		//this.editor.ui.initHistory();
		
		this.setState({
			mode: 'editor'
		});
	}

	back(){
		this.setState({
			mode: this.active ? 'capture' : 'start'
		});
	}
	constructor(props) {
		super(props);		
		autoBind(this);
	}
	
	copy(){
		this.editor._graphics._canvas.lowerCanvasEl.toBlob(blob => {
			navigator.clipboard.write([
        new window.ClipboardItem({
            [blob.type]: blob
        })
			]);
		}, 'image/png', 1);

	}
	canvasUpdate(){
		console.log('update');
		this.setState({
			export: this.editor.toDataURL()
		});
	}
	componentDidMount(){
		this.editor = this.editorRef.current.getInstance();
		window.editor = this.editor;
		this.editor._graphics._canvas.upperCanvasEl.addEventListener('contextmenu', e => e.preventDefault());
		this.editor.on('undoStackChanged', this.canvasUpdate);
		this.editor.on('redoStackChanged', this.canvasUpdate);

	}

  render() {
		const dMode = m => m === this.state.mode ? '' : 'none';
    return (
			<React.Fragment>
			<div id='margin'></div>
			<div className='parent' style={{display: this.state.mode === 'editor' ? 'none': ''}}>
				<div style={{display: dMode('start')}} id='start'>
					<h1>SCShot</h1>
					<p>Take Screenshots online without installing anything and there's a built-in editor.</p>
					<Button variant='primary' onClick={this.capture}>
						<img className='icon' src='imgs/desktop-outline.svg' alt=""/>&nbsp;
						Start Screen Capture
					</Button>
				</div>
				<div style={{display: dMode('capture')}} id='capture'>
					<div id='captureButtons'>
						<Button variant='secondary' onClick={this.capture}>
							<img className='icon' src='imgs/refresh-outline.svg' alt=""/>&nbsp;
							New Screen Capture
						</Button>
						<Button variant='danger' onClick={this.stop}>
							<img className='icon' src='imgs/stop-circle-outline.svg' alt=""/>&nbsp;
							Stop Capture
						</Button>
						<Button variant='primary' onClick={this.take} id='take'>
							<img className='icon' src='imgs/desktop-outline.svg' alt=""/>&nbsp;
							Take Screenshot
						</Button>
					</div>
					<video ref={this.video} autoPlay></video>

				</div>


			</div>
			<div className='editor' style={{display: dMode('editor')}}>
				<div className='editor-header'>
					<Button variant='secondary' onClick={this.back}>
						<img className='icon' src='imgs/arrow-back-outline.svg' alt=""/>&nbsp;
						Back
					</Button>
					<Button variant='primary' className='export' href={this.state.export} download>
						<img className='icon' src='imgs/save-outline.svg' alt=""/>&nbsp;
						Save
					</Button>
					<Button variant='primary' className='export' onClick={this.copy}>
						<img className='icon' src='imgs/clipboard-outline.svg' alt=""/>&nbsp;
						Copy to Clipboard
					</Button>
				</div>
				<div className='direct'>
					<ImageEditor
						includeUI={{
							initMenu: 'crop',
							loadImage: {
								path: 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7',
								name: 'Screenshot'
							},
							uiSize: {
								width: '100%',
								height: '100%',
							},
							menuBarPosition: 'bottom',
						}}
						selectionStyle={{
							cornerSize: 20,
							rotatingPointOffset: 70,
						}} usageStatistics={false} ref={this.editorRef}/>
				</div>
			</div>
			</React.Fragment>
		);
  }

	
}

export default App;
